.App {
    display: flex;
    flex-direction: row;
}

.todoList {
    max-width: 400px;
    position: relative;
    margin-right: 30px;
}


.todoList > div {
    padding: 10px;
}

.todoList-task {

}

.filter-active {
    background-color: aqua;
}

.error {
    border-color: red;
}

.todoList-task.done {
    opacity: 0.5;
}

button {
    font-family: cursive;
}


.times {
    margin-left: 10px;
    color: #000;
    cursor: pointer;
}
.header {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.times-header {
    margin-left: 10px;
    color: #000;
    position: absolute;
    top: 35px;
    right: 20px;
    cursor: pointer;

}

